





































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Ref } from 'vue-property-decorator';
import { ElForm } from 'element-ui/types/form';

@Component
export default class Primary extends Vue {
    @Ref('form') public readonly elForm!: ElForm;
    @Prop() public readonly data!: IBaseStation;

    public readonly colors: string[] = ['#99A9BF', '#F7BA2A', '#FF9900'];
    public form: IJson = {
        power: 1,
        main: 85,
        channel: 2,
        send: 1,
        mode: 17
    };
    public rules = {
        groupCode: {
            pattern: /^[0-9A-Fa-f]{1,4}$/,
            message:
                'groupCode is not a hexadecimal string less than 4 in length'
        },
        distance: {
            type: 'number',
            max: 0xffff,
            min: 0
        },
        time: {
            type: 'number',
            max: 0xffffffff,
            min: 0
        }
    };

    public onSubmit() {
        this.elForm
            .validate()
            .then(() => this.$confirm('?'))
            .then(() =>
                this.$http.post('/api/protocol/sendProtocol', {
                    ip: this.data.ip,
                    port: 50000,
                    protocol: '41' + this.parse(this.form)
                })
            )
            .then(() => this.$message.success(''))
            .catch(console.log);
    }

    // 
    private parse(value: string | IJson) {
        // [[key, byte]]
        const keys = [
            ['groupCode', 2],
            ['main', 1],
            ['mode', 1],
            ['distance', 2],
            ['channel', 1],
            ['time', 4],
            ['power', 1],
            ['send', 1]
        ];

        if (typeof value === 'string') {
            const data: IJson = {};

            let start: number = 0;
            keys.forEach(([k, byte]) => {
                const str = value.substr(start, <number>byte * 2);
                data[k] = start ? +('0x' + str) : str.toUpperCase();
                start += <number>byte * 2;
            });

            return data;
        }

        return keys
            .reduce((a, [k, byte]) => {
                a.push(value[k].toString(16).padStart(<number>byte * 2, '0'));

                return a;
            }, [])
            .join('');
    }
}
