






































import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import IpInput from '../../components/form/IpInput.vue';
import { ElForm } from 'element-ui/types/form';

@Component({
    components: {
        'ip-input': IpInput
    }
})
export default class Net extends Vue {
    @Prop() public data!: IBaseStation;

    public form = {
        baseIp: <string[]>[],
        serverIp: <string[]>[],
        mask: <string[]>[],
        mac: <string[]>[],
        mode: 85
    };
    public rules = {
        basePort: {
            type: 'number',
            max: 0xffff,
            required: true
        },
        serverPort: {
            type: 'number',
            max: 0xffff,
            required: true
        }
    };

    public onSubmit() {
        if (this.isValid !== true) {
            return;
        }

        const form = <ElForm>this.$refs.form;
        form.validate()
            .then(() => this.$confirm('?'))
            .then(() =>
                this.$http.post('/api/protocol/sendProtocol', {
                    ip: this.data.ip,
                    port: 50000,
                    protocol: '42' + this.parse(this.form)
                })
            )
            .then(() => this.$message.success(''))
            .catch(console.log);
    }

    private get isValid() {
        const { baseIp, mask, mac, serverIp } = this.form;
        if (baseIp.filter(v => !!v).length !== 4) {
            return this.$message.error('IP');
        }
        if (mask.filter(v => !!v).length !== 4) {
            return this.$message.error('mask');
        }
        if (mac.filter(v => !!v).length !== 6) {
            return this.$message.error('mac');
        }
        if (serverIp.filter(v => !!v).length !== 4) {
            return this.$message.error('IP');
        }

        return true;
    }

    // 
    private parse(value: string | IJson) {
        // [[key, byte, ]]
        const keys = [
            ['baseIp', 4, true],
            ['mask', 4, true],
            ['mac', 6, true],
            ['basePort', 2, false],
            ['serverIp', 4, true],
            ['serverPort', 2, false],
            ['mode', 1, false]
        ];

        if (typeof value === 'string') {
            return this.str2obj(keys, value);
        }

        return this.obj2str(keys, value);
    }

    // 
    private str2obj(keys: any[], value: string) {
        const parse210 = (v: string) => +('0x' + v);
        const parseToArr = (s: number, l: number) => {
            const arr = value.substr(s, l).match(/\d{2}/g) || [];

            return arr.map(parse210);
        };

        const data: IJson = {};
        let start: number = 0;
        keys.forEach(([k, byte, isArr]) => {
            const len = <number>byte * 2;

            data[<string>k] = isArr
                ? parseToArr(start, len)
                : parse210(value.substr(start, len));

            start += len;
        });

        return data;
    }

    // 
    private obj2str(keys: any[], value: IJson) {
        return keys
            .reduce((a, [k, byte]) => {
                let str: number | string | number[] = value[k];
                if (Array.isArray(str)) {
                    str = str
                        .map(v => v.toString(16).padStart(2, '0'))
                        .join('');
                } else {
                    str = (+str).toString(16).padStart(byte * 2, '0');
                }

                a.push(str);

                return a;
            }, [])
            .join('');
    }
}
