


























































































import Component from 'vue-class-component';
import IpInput from '../../components/form/IpInput.vue';
import { ElForm } from 'element-ui/types/form';
import Vue from 'vue';
import { hexadecimalRuleFactory } from '@/assets/utils/util';
import Select from '@/components/form/Select.vue';

@Component({
    components: {
        'ip-input': IpInput,
        'app-select': Select
    }
})
export default class BaseAdd extends Vue {
    public form = {
        main: 0,
        ip: [] as string[],
        id: ''
    };
    public rules: any = {};

    public created() {
        ['coordx', 'coordy', 'coordz'].forEach(
            (k: string) => (this.rules[k] = { type: 'number' })
        );

        Object.assign(this.rules, {
            id: hexadecimalRuleFactory(8, 'id'),
            timeCorrectionValue: {
                pattern: /^[\d\.]+$/,
                message: 'timeCorrectionValue is not a number'
            }
        });
    }

    // 
    // 
    public add() {
        const { ip, id } = this.form;
        if (ip.filter(v => !!v).length !== 4) {
            return this.$message.warning('ip');
        }

        const form = <ElForm>this.$refs.form;
        form.validate()
            .then(() =>
                this.$http.post(
                    '/api/base/addBase',
                    {
                        ...this.form,
                        ip: ip.join('.'),
                        id: id.padStart(8, '0')
                    },
                    {
                        'Content-Type': 'application/json'
                    }
                )
            )
            .then(() => {
                this.$message.success('');
                location.href = location.href;
            })
            .catch(console.log);
    }
}
